/* App.css - Основные стили для приложения */

/* Navigation Bar */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  padding: 25px 0;
  transition: background 0.3s ease;
}

.navbar.scrolled {
  background: white;
  border-bottom: 2px solid #333;
  box-shadow: 3px 3px 0px #333;
}

.nav-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
}



.nav-links {
  display: flex;
  gap: 30px;
  align-items: center;
}

.nav-link {
  font-family: 'Indie Flower', cursive;
  font-size: 1.2rem;
  font-weight: 900;
  color: #25282d;
  text-decoration: none;
  padding: 12px 20px;
  border-radius: 20px;
  transition: all 0.3s ease;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 8px;
}

.nav-icon {
  width: 20px;
  height: 20px;
  transition: all 0.3s ease;
}

.nav-line {
  width: 100%;
  height: auto;
  margin-top: 5px;
  max-width: 70px;
  position: absolute;
  opacity: 70%;
  bottom: 0px;
  left: 50%;
  transform: translateX(-50%);
}

.nav-link:hover {
  color: #2c3e50;
  transform: translateY(-2px);
}

.nav-link:hover .nav-icon {
  opacity: 1;
  transform: scale(1.1);
}

  /* Contact & Social Media Section */
  .contact-social-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 60px;
    max-width: 1000px;
    margin: 0 auto;
  }

  .contact-left {
    text-align: left;
    width: 100%;
    max-width: none;
  }

  .social-right {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .social-title {
    font-family: 'Permanent Marker', cursive;
    font-size: 1.5rem;
    color: #2c3e50;
    margin-bottom: 30px;
  }

  .contact-info {
    position: relative;
    max-width: 800px !important;
    width: 100%;
    min-height: 300px;
  }

  .contact-svg {
    width: 100% !important;
    height: 100% !important;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    object-fit: contain;
  }

  .contact-content {
    position: relative;
    z-index: 2;
    padding: 40px;
    display: flex;
    flex-direction: column;
  }

  .contact-text {
    font-size: 1.1rem;
    line-height: 1.6;
    color: #1d2124;
    margin-bottom: 30px;
  }

  .contact-details {
    display: flex;
    flex-direction: column;
    gap: 0px;
  }

  .contact-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0;
    min-height: 50px;
    line-height: 1;
  }

  .contact-label {
    font-weight: bold;
    color: #1d2124;
    font-size: 1rem;
    display: flex;
    align-items: center;
    min-width: 120px;
    margin: 0;
    padding: 0;
  }

  .contact-link, .contact-text {
    text-decoration: none;
    color: #1d2124;
    font-weight: 500;
    transition: color 0.3s ease;
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    line-height: 1;
  }

  .contact-item:last-child {
    border-bottom: none;
    padding-bottom: 0;
  }

  .contact-link:hover {
    color: #0768a9;
    text-decoration: underline;
  }

.notebook-page {
  min-height: 100vh;
  background: 
    linear-gradient(90deg, #E6F3FF 1px, transparent 1px),
    linear-gradient(#E6F3FF 1px, transparent 1px);
