/* Scroll to Top Button */
.scroll-to-top {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 100px;
  height: 100px;
  background: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  transition: all 0.3s ease;
}

.scroll-to-top:hover {
  transform: translateY(-2px);
}

.scroll-to-top img {
  width: 50px;
  height: 50px;
  opacity: 0.7;
  transition: all 0.3s ease;
}

.scroll-to-top:hover img {
  opacity: 1;
}

.scroll-to-top.clicked img {
  animation: flyUp 0.6s ease-out;
}

@keyframes flyUp {
  0% {
    transform: translateY(0) scale(1);
    opacity: 0.7;
  }
  50% {
    transform: translateY(-20px) scale(1.2);
    opacity: 1;
  }
  100% {
    transform: translateY(0) scale(1);
    opacity: 0.7;
  }
}

