  font-family: 'Permanent Marker', cursive;
  font-size: 1.5rem;
  color: #25282d;
  margin: 0 0 20px 0;
  text-align: center;
  position: relative;
}

.interest-category-title::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 2px;
  background: linear-gradient(90deg, #ff9a9e, #fecfef);
  border-radius: 1px;
}

.interest-items {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.interest-item {
  display: flex;
  align-items: flex-start;
  gap: 12px;
